.header {
  min-height: 80px;
  border-top: 1px solid #ececec;
  background-color: #ffffff;
}
.header__logo-box {
  margin: 0 auto;
  margin-top: 21px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 22px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}
.header__logo {
  display: block;
}
.header__logo:hover {
  opacity: 0.6;
}
.header__logo:active {
  opacity: 0.3;
}
.header__menu-btn {
  width: 23px;
  height: 23px;
  position: absolute;
  top: 26px;
  right: 36px;
  cursor: pointer;
}
.header__menu-btn--cross {
  display: block;
}
.header__menu-btn--cross.header__menu-btn--hide {
  display: none;
}
.header__menu-btn--nojs.header__menu-btn--cross {
  display: none;
}
.header__menu-btn--burger {
  display: none;
}
.header__menu-btn--burger.header__menu-btn--show {
  display: block;
}
.header__site-navigation.header__site-navigation--closed {
  display: none;
}
.header__user-navigation.header__user-navigation--closed {
  display: none;
}
.header__pic-burger {
  margin-top: 2px;
}
.header__site-navigation {
  margin-top: 22px;
}
.header__site-link,
.header__user-link,
.header__product-link {
  color: #000000;
  text-decoration: none;
  display: block;
}
.header__site-navigation,
.header__user-navigation {
  margin: 0;
  padding: 0;
  list-style: none;
}
.header__site-link,
.header__user-link {
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  border-bottom: 1px solid #ececec;
  padding-top: 26px;
  padding-bottom: 28px;
  padding-left: 75px;
}
.header__site-link:hover,
.header__product-link:hover {
  opacity: 0.6;
}
.header__site-link:active,
.header__product-link:active {
  opacity: 0.3;
}
.header__site-link:first-child {
  border-top: 1px solid #ececec;
}
.header__user-item--search,
.header__user-item--cart {
  position: relative;
}
.header__user-item--search svg,
.header__user-item--cart svg {
  position: absolute;
  top: 29px;
  left: 31px;
  opacity: 0.2;
}
.header__user-link:hover .header__user-text {
  opacity: 0.6;
}
.header__user-link:active .header__user-text {
  opacity: 0.3;
}
.header__user-link:hover svg {
  fill: #63d1bb;
  opacity: 1;
}
.header__user-link:active svg {
  fill: #63d1bb;
  opacity: 0.3;
}
.header__product-navigation {
  display: none;
}
.header__delivery {
  display: none;
}
