.catalog-nav {
  margin-top: -89px;
}

.catalog-nav__list {
  .list-reset();
}

.catalog-nav__item {
  text-align: left;
  min-height: 103px;

  &:hover {
    min-height: 108px;
    margin-top: -5px;
  }

  &:hover .catalog-nav__link {
    transform: translateY(-5px);
  }

  &:active .catalog-nav__link {
    opacity: 0.3;
  }
}

.catalog-nav__item--interior {
  background-color: #63d1bb;
}

.catalog-nav__item--interior:hover {
  background-color: #59d5bc;
}

.catalog-nav__item--toys {
  background-color: #56c3b2;
}

.catalog-nav__link {
  display: block;
  font-size: 19px;
  line-height: 24px;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
  padding-top: 25px;
  padding-right: 120px;
  padding-bottom: 30px;
  padding-left: 30px;
  position: relative;
}

.catalog-nav__icon {
  position: absolute;
  content: "";
  display: block;
  right: 36px;
  top: 23px;
}

.catalog-nav__icon-pic {
  fill: #31685d;
  opacity: 0.5;
}

.catalog-nav__item:hover .catalog-nav__icon-pic {
  opacity: 0.7;
}

.catalog-nav__icon--toys {
  right: 26px;
  top: 21px;
}

@media (min-width: @tablet-width) {
  .catalog-nav {
    min-height: 119px;
    margin: 0 auto;
    margin-top: -119px;
    background-position-x: 50%;
    position: relative;

    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 34px;
      top: 107px;
      left: 0;
      background-image: url("../img/bg-zigzag-fill.svg");
      background-repeat: repeat-x;
      background-size: 768px 25px;
    }
  }

  .catalog-nav__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 668px;
    margin: 0 auto;
  }

  .catalog-nav__item {
    width: 50%;
  }

  .catalog-nav__link {
    font-size: 20px;
    line-height: 30px;
    padding-top: 28px;
    padding-bottom: 30px;
    padding-left: 36px;
  }

  .catalog-nav__icon {
    top: 30px;
  }

  .catalog-nav__icon--toys {
    right: 34px;
    top: 31px;
  }
}

@media (min-width: @desktop-width) {
  .catalog-nav {
    min-height: 119px;
    margin: 0 auto;
    margin-top: -118px;

    &::after {
      width: 100%;
      height: 34px;
      top: 103px;
      background-image: url("../img/bg-zigzag-fill.svg");
      background-repeat: repeat-x;
      background-size: 1247px 34px;
      background-position-x: -49px;
    }
  }

  .catalog-nav__link {
    padding-right: 305px;
  }

  .catalog-nav__list {
    width: 1050px;
    margin: 0 auto;
  }
}
